$background-color-light: #fff !default;
$background-color-base: #fafafa !default;
$background-color-dark: #eaeaea !default;

$body-background-color: $background-color-base !default;

$sidebar-width: 220px;
$sidebar-background-color: $background-color-base !default;

$border-color: #d6d6d6 !default;

$text-color-light: #555 !default;
$text-color-base: #333 !default;
$text-color-dark: #111 !default;

$link-color: #7baaf7 !default;

$icon-color: #919191 !default;
$shadow-color: #fafbfc !default;

$primary: $text-color-dark !default;

$font-size-base: 1rem !default;
$font-size-lg: $font-size-base * 1.25 !default;
$font-size-sm: $font-size-base * 0.875 !default;

@import "../../../../styles/animations/rotate";

@import "../../../../styles/mixins/geometry";
@import "../../../../styles/mixins/scrollbar";
@import "../../../../styles/mixins/text";

.movie-detail-wrapper {
  height: 100%;
}
.movie-detail {
  display: flex;
  height: 100%;
  flex-direction: column;
  color: #fff;

  &__header,
  &__footer {
    display: flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 45px;
  }

  &__header {
    font-weight: 600;
    border-bottom: 1px solid #303030;
  }

  &__footer {
    border-top: 1px solid #303030;
  }

  &__body {
    @include hide-scrollbar;
    flex: auto;
    overflow: auto;
    padding: 10px;
  }

  &__poster {
    margin-bottom: 12px;
    padding-bottom: 143%;
    border-radius: 4px;
    background-size: cover;
  }

  .summary {
    line-height: 2;
  }

  .search-button {
    color: #fff;
    background-color: rgba(#202020, 0.3);
  }

  .card {
    margin-bottom: 5px;
    border-radius: 4px;
    border: 1px solid #303030;
    background: radial-gradient(rgba(#202020, 0.1), rgba(#202020, 0.3));

    &__title {
      padding: 0 12px;
      line-height: 25px;
      background-color: rgba(#202020, 0.3);
    }

    &__body {
      padding: 15px;
    }
  }

  .detail-info-item {
    display: flex;
    padding: 5px 0;
    min-height: 35px;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #a7a7a7;

    &:last-child {
      border: none;
    }

    &__label {
      flex-shrink: 0;
      margin-right: 20px;
    }
  }
}
